CodeMirror.defineMode('makefile', function() {

  var words = {};
  function define(style, string) {
    var split = string.split(' ');
    for(var i = 0; i < split.length; i++) {
      words[split[i]] = style;
    }
  };

  // Atoms
  define('atom', 'true false');

  // Keywords
  define('keyword', 'if then do else elif while until for in esac fi fin ' +
    'fil done exit set unset export function');

  // Commands
  define('builtin', 'ab awk bash beep cat cc cd chown chmod chroot clear cp ' +
    'curl cut diff echo find gawk gcc get git grep kill killall ln ls make ' +
    'mkdir openssl mv nc node npm ping ps restart rm rmdir sed service sh ' +
    'shopt shred source sort sleep ssh start stop su sudo tee telnet top ' +
    'touch vi vim wall wc wget who write yes zsh');

  function tokenBase(stream, state) {
    if (stream.eatSpace()) return null;

    var sol = stream.sol();
    var ch = stream.next();

    if (ch === '%' && stream.eat(':')) { return "def"; }
    if (ch === '@') { return "atom"; }
    if (ch === '#') {
      if (sol && stream.eat('!')) {
        stream.skipToEnd();
        return 'meta'; // 'comment'?
      }
      stream.skipToEnd();
      return 'comment';
    }

    if (ch === '$' && stream.eat('(')) {
      state.tokens.unshift(tokenDollar);
      return tokenize(stream, state);
    }
    if (ch === '$' && stream.eat('{')) {
      state.tokens.unshift(tokenDollarB);
      return tokenize(stream, state);
    }
    if (ch === '$' && stream.eat('$') && stream.match(/^[\w]+/)) { return "quote"; }
    if (ch === '$' && (stream.eat('@') || stream.eat('<') || stream.eat('^'))) { return "quote"; }

    if (ch === '\'' || ch === '"' || ch === '`') {
      state.tokens.unshift(tokenString(ch));
      return tokenize(stream, state);
    }

    if (ch === 'i' && (stream.match('feq ') || stream.match('fneq '))) {
      stream.skipToEnd();
      return "meta";
    }
    if (ch === 'e' && (stream.match('lse') || stream.match('ndif'))) { return "meta"; }
    if (ch === 'i' && stream.match('nclude ')) { return "string"; }

    if (sol && stream.match(/^\s*\w[^\s=:]*\s*(:|=)/)) { 
      return "def"; 
    }

    stream.eatWhile(/[\w-]/);
    var cur = stream.current();
    return words.hasOwnProperty(cur) ? words[cur] : null;
  }

  function tokenString(quote) {
    return function(stream, state) {
      var next, end = false, escaped = false;
      while ((next = stream.next()) != null) {
        if (next === quote && !escaped) {
          end = true;
          break;
        }
      }
      if (end || !escaped) {
        state.tokens.shift();
      }
      return ((quote === ')' || quote === '}') ? 'quote' : 'string');
    };
  };


  var tokenDollar = function(stream, state) {
    if (state.tokens.length > 1) stream.eat('$');
    var ch = stream.next(), hungry = /\w/;
    state.tokens[0] = tokenString(')');
    return tokenize(stream, state);
  };
  var tokenDollarB = function(stream, state) {
    if (state.tokens.length > 1) stream.eat('$');
    var ch = stream.next(), hungry = /\w/;
    state.tokens[0] = tokenString('}');
    return tokenize(stream, state);
  };

  function tokenize(stream, state) {
    return (state.tokens[0] || tokenBase) (stream, state);
  };

  return {
    startState: function() {return {tokens:[], inMid: false};},
    token: function(stream, state) {
      return tokenize(stream, state);
    }
  };
});

CodeMirror.defineMIME('text/x-makefile', 'makefile');