CodeMirror.defineMode("sequence", function() {

    return {
        token: function(stream, state) {
            var ch = stream.peek();

            if(state.beginString){
                stream.skipToEnd();
                state.beginString = false;
                return "";
            }

            if(stream.eatSpace()){
                return "";
            }

            if(stream.eat("#")){
                stream.skipToEnd();
                return "comment";
            }
            
            if(stream.match(/title(\s|$)/i)){
                return "header";
            }

            if(stream.match(/(left|right) of(\s|$)/i) || 
                stream.match(/over(\s|$)/i)){
                return "keyword";
            }

            if(stream.match(/note|participant(\s|$)/i)){
                return "tag";
            }

            if(stream.match(/,|->:|--|-|>>|>/)){
                return "operator";
            }

            if(stream.match(/:\s*/)){
                state.beginString = true;
                return "atom";
            }

            if(stream.match(/[^:\->,]+/)){
                return "def";
            }

            stream.next();
            return "";
        },
        startState: function() {
            return {
                beginString: false
            };
        }
    };
});
