CodeMirror.defineMode("mermaid", function() {

    var startState = function(){
        var state = {};
        resetState(state);
        resetSequenceState(state);
        resetGanttState(state);
        return state;
    }

    var resetSequenceState = function(state){
        state.beginString = false;
        state.beginController = false;
        state.beginActor = false;
        state.beginAttr = false;
    }

    var resetGanttState = function(state){
        state.beginTittle = false;
        state.beginSection = false;
        state.beginDateFormat = false;
    }

    var resetState = function(state){
        state.inFlowchart = false;
        state.inSequence = false;
        state.inGantt = false;
        state.inClassDiagram = false;
        state.inGitGraph = false;
        state.inInfo = false;
        state.inDotGraph = false;
    }

    var skipTo_ = function(exp){
        var textAfter = this.string.substring(this.pos),
            match = textAfter.match(exp);

        if(match){
            this.pos += (match.index - match.pos) || 1;
            return match.index;
        } else {
            this.skipToEnd();
        }
    }

    return {
        token: function(stream, state) {
            var ch = stream.peek();

            if(stream.eatSpace()){
                return "";
            }

            if(stream.match("%%")){
                stream.skipToEnd();
                return "comment";
            }

            if(stream.match("graph", true, true)){
                resetState(state);
                state.inFlowchart = true;
                return "keyword";
            }

            if(stream.match("gantt", true, true)){
                resetState(state);
                state.inGantt = true;
                return "keyword";
            }

            if(stream.match("sequenceDiagram", true, true)){
                resetState(state);
                state.inSequence = true;
                resetSequenceState(state);
                return "keyword";
            }

            if(stream.match("classDiagram", true, true)){
                resetState(state);
                state.inSequence = true;
                return "keyword";
            }

            if(stream.match("gitGraph", true, true)){
                resetState(state);
                state.inGitGraph = true;
                return "keyword";
            }

            if(stream.match("info", true, true)){
                resetState(state);
                state.inInfo = true;
                return "keyword";
            }

            if(stream.match("digraph", true, true)){
                resetState(state);
                state.inDotGraph = true;
                return "keyword";
            }

            if(state.inFlowchart){
                if(stream.match("TB") || stream.match("BT") || stream.match("LR") || stream.match("RL")){
                    return "attribute";
                }

                if(stream.match(/(subgraph|title|end|style|classDef)(\s|$)/)){
                    return "keyword";
                }

                var endBox = null,
                    startBox = null;
                    textAfter = stream.string.substring(stream.pos);

                if(endBox = textAfter.match(/^\(\(.*?\)\)/)){
                    stream.pos += endBox[0].length;
                    return "quote";
                }

                if(endBox = textAfter.match(/^\(.*?\)/)){
                    stream.pos += endBox[0].length;
                    return "quote";
                }

                if(endBox = textAfter.match(/^\[.*?\]/)){
                    stream.pos += endBox[0].length;
                    return "quote";
                }

                if(endBox = textAfter.match(/^\{.*?\}/)){
                    stream.pos += endBox[0].length;
                    return "quote";
                }

                if(endBox = textAfter.match(/^\>[^\[]*?\]/)){
                    stream.pos += endBox[0].length;
                    return "quote";
                }

                if(stream.match(/^--.+--[->]/)){
                    return "operator";
                }

                if(stream.match(/^--[->]\|.+\|/)){
                    return "operator";
                }

                if(stream.match(/^--[->]/)){
                    return "operator";
                }

                if(stream.match(/^-\..+\.->/)){
                    return "operator";
                }

                if(stream.match(/^-\.->/)){
                    return "operator";
                }

                if(stream.match(/^==.+==>/)){
                    return "operator";
                }

                if(stream.match(/^==>/)){
                    return "operator";
                }

                if(stream.match(/^[^\[\(\{\>\-=%]+/)){
                    return "";
                }

                skipTo_.call(stream, /[ \[\{\(\-=>|]/);
                return "";
            }

            if(state.inSequence){
                if(state.beginString){
                    stream.skipToEnd();
                    resetSequenceState(state);
                    return "";
                }

                if(state.beginController){
                    stream.skipToEnd();
                    resetSequenceState(state);
                    return "";
                }

                if(state.beginActor){
                    stream.skipToEnd();
                    resetSequenceState(state);
                    return "def";
                }

                if(stream.match(/participant(\s|$)/i)){
                    state.beginActor = true;
                    return "tag";
                }

                if(stream.match(/note(\s|$)/i)){
                    return "tag";
                }

                if(stream.match(/title(\s|$)/i)){
                    state.beginController = true;
                    return "header";
                }

                if(stream.match(/(left|right) of(\s|$)/i) || 
                    stream.match(/over(\s|$)/i)){
                    return "keyword";
                }

                if(stream.match(/,|->:|--|-|>>|>/)){
                    return "operator";
                }

                if(stream.match(/:\s*/)){
                    resetSequenceState(state);
                    state.beginString = true;
                    return "atom";
                }

                if(stream.match(/(alt|opt|else|loop)(\s|$)/i)){
                    resetSequenceState(state);
                    state.beginController = true;
                    return "keyword";
                }

                if(stream.match(/end(\s|$)/i)){
                    resetSequenceState(state);
                    return "keyword";
                }

                if(stream.match(/[^:\->,]+/)){
                    return "def";
                }

                skipTo_.call(stream, /[ \[\{\(\-=>:|]/);
                return "";
            }

            if(state.inGantt){
                if(state.beginTittle){
                    stream.skipToEnd();
                    resetGanttState(state);
                    return "header";
                }

                if(state.beginSection){
                    stream.skipToEnd();
                    resetGanttState(state);
                    return "def";
                }

                if(state.beginDateFormat){
                    stream.skipToEnd();
                    resetGanttState(state);
                    return "variable-3";
                }

                if(stream.match(/title(?:\s|$)/)){
                    resetGanttState(state);
                    state.beginTittle = true;
                    return "keyword";
                }

                if(stream.match(/dateFormat(\s|$)/i)){
                    state.beginDateFormat = true;
                    return "keyword";
                }

                if(stream.match(/section(?:\s|$)/)){
                    resetGanttState(state);
                    state.beginSection = true;
                    return "keyword";
                }

                if(stream.match(/[:,]/)){
                    return "operator";
                }

                var attrStart = stream.string.match(/:/);
                if(!attrStart){
                    stream.skipToEnd();
                    return "";
                }

                if(stream.pos < attrStart.index){
                    stream.pos = attrStart.index;
                    return "";
                }

                if(stream.match(/(done|active)(\W|$)/)){
                    return "operator";
                }

                if(stream.match(/(crit)(\W|$)/)){
                    return "builtin";
                }

                if(stream.match(/^\d[\d\-dhym]+/)){
                    return "variable-3"
                }

                if(stream.match(/(after)(\W|$)/)){
                    return "keyword"
                }

                skipTo_.call(stream, /[ ,:]/);
                return "";
            }

            stream.skipToEnd();
            return "";
        },
        startState: startState
    };
});
