CodeMirror.defineMode("flow", function() {

    return {
        token: function(stream, state) {
            var ch = stream.peek();

            if(stream.eatSpace()){
                return "";
            }

            if(stream.eat("#")){
                stream.skipToEnd();
                return "comment";
            }

            if(stream.match(":>")){
                state.afterDef = true;
                state.linkStart = true;
                return "operator";
            }

            if(state.linkStart){
                state.linkStart = false;
                stream.skipToEnd();
                return "link";
            }

            var afterText = stream.string.substring(stream.pos),
                defMatch = afterText.match(/=>/),
                midText, contentMatch, flowMatch;

            state.afterDef = state.afterDef && !defMatch;

            if(!state.afterDef){
                if(!defMatch){
                    // continue
                } else if(defMatch.index == 0){
                    stream.pos += 2;
                    state.flowStart = false;
                    return "operator";
                } else {
                    stream.pos += defMatch.index;
                    state.flowStart = false;
                    return "variable-3";
                }

            }

            contentMatch = afterText.match(/:/);
            if(!contentMatch){
                
            } else if(contentMatch.index == 0){
                stream.pos += 1;
                state.afterDef = true;
                return "operator"
            } else {
                state.afterDef = false;
                if(stream.match("start")){
                    return "keyword";
                }
                if(stream.match("end")){
                    return "keyword";
                }
                if(stream.match("operation")){
                    return "keyword";
                }
                if(stream.match("inputoutput")){
                    return "keyword";
                }
                if(stream.match("subroutine")){
                    return "keyword";
                }
                if(stream.match("condition")){
                    return "keyword";
                }

                stream.pos += contentMatch.index;
                return "";
            }

            if(!state.flowStart){
                flowMatch = afterText.match(/->/);
                if(flowMatch) {
                    state.afterDef = false;
                    state.flowStart = true;
                }
            }

            if(state.flowStart){
                flowMatch = afterText.match(/->/);
                if(!flowMatch){
                    stream.skipToEnd();
                    return "variable-3";
                } else if(flowMatch.index){
                    stream.pos += flowMatch.index;
                    return "variable-3";
                } else {
                    stream.pos += 2;
                    return "operator";
                }
            }

            state.flowStart = false;

            stream.next();
            return "";
        },
        startState: function() {
            return {
                afterDef: false,
                flowStart: false,
                linkStart: false
            };
        }
    };
});
